
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700;800&display=swap');


*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style: none;
}

:root {
    --color-primary:#191d2b;
    --color-secondary:#27ae60;
    --color-white:#fff;
    --color-black:#000;
    --color-gray0:#f8f8f8;
    --color-gray-1:#dbe1e8;
    --color-gray-2:#b2becd;
    --color-gray-3:#6c7983;
    --color-gray-4:#454e56;
    --color-gray-5:#2a2e35;
    --color-gray-6:#12181b;
    --br-sm-2:12px;
    --box-shadow-1: 0 3px 15px rgba(0,0,0,.2) 


}

body{
    background-color: var(--color-primary);
    font-family: 'Poppins', sans-serif;
    font-size: 1.1rem;
    color: var(--color-white);
    transition: all .4s ease-in-out;
}

a{
    display: inline-block;
    text-decoration: none;
    color: inherit;
    font-family: inherit;
}

header {
    height: 100vh;
    color: var(--color-white);
    overflow: hidden;
}

section {
    min-height: 100vh;
    width: 100%;
    position: absolute;
    left: 0;
    top: 0;
    padding: 3rem 10rem;

}

.section {
    transform: translateY(-100%);
    transition: all .4s ease-in-out;
    background-color: var(--color-primary);
}

.sec1 {
    display: none;
    transform: translateY(0) scale(1);

}

.sec2 {
    display: none;
    transform: translateY(0) scale(1);

}

.sec3 {
    display: none;
    transform: translateY(0) scale(1);

}

.sec4 {
    display: none;
    transform: translateY(0) scale(1);

}

.sec5 {
    display: none;
    transform: translateY(0) scale(1);

}

.active {
    display: block;
    animation: scaleAnim 1s ease-in-out;

    @keyframes scaleAnim {
        0%{
            transform: translateY(-100%) scaleY(0);
        }
        100%{
            transform: translateY(0) scale(1);
        }
    }
}

//Controlls
.controls{
    position: fixed;
    z-index: 10;
    top: 50%;
    right: 3%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transform: translateY(-50%);

    .active-btn {
        background-color: var(--color-secondary) !important;
        transition: all .4s ease-in-out;

        i{
            color: var(--color-white) !important;
        }
    }

    .control{
        padding: 1rem;
        cursor: pointer;
        background-color: var(--color-gray-4);
        width: 45px;
        height: 45px;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        margin: .7rem 0;
        box-shadow: var(--box-shadow-1);
    }

    i{
        font-size: 1.2rem;
        color: var(--color-gray-2);
        pointer-events: none;
    }
}

/* header-content */

.header-content{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    height: 100%;

    .left-header{
        display: flex;
        align-items: center;
        position: relative;
        .h-shape{
            transition: all .4s ease-in-out;
            width: 65%;
            height: 100%;
            background-color: var(--color-secondary);
            position: absolute;
            left: 0;
            top: 0;
            z-index: -1;
            clip-path: polygon(0 0, 46% 0, 79% 100%, 0% 100%);
        }

        .image{
            border-radius: var(--br-sm-2);
            height: 90%;
            width: 68%;
            margin-left: 4rem;
            background-color: var(--color-black);
            transition: all .4s ease-in-out;
            
            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
                transition: all .4s ease-in-out;
                filter: grayscale(100%);

                &:hover{
                    filter: grayscale(0);
                }
            }
        }
    }

    .right-header{
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding-right: 9rem;

        .name{
            font-size: 2rem;

            span{
                color: var(--color-secondary);
            }
        }
        p{
            font-size: .9rem;
            margin: 1.5rem 0;
            line-height: 2rem;
        }
    }
}

/*About */

.about-container{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    padding-top: 3.5rem;
    padding-bottom: 5rem;

    .right-about{
        display: grid;
        grid-template-columns: repeat(2,1fr);
        grid-gap:2rem;

        .about-item{
            border: 1px solid var(--color-gray-5);
            border-radius: 5px;
            transition: all .4s ease-in-out;
            box-shadow: 1px 2px 15px rgba(0,0,0,.1);

            &:hover{
                cursor: default;
                transform: translateY(-5px);
                border: 1px 4px 15px rgba(0,0,0,.32);
            }
            .abt-text{
                padding: 1.5rem;
                display: flex;
                flex-direction: column;

                .large-text{
                    font-size: 3rem;
                    font-weight: 700;
                    color: var(--color-secondary);
                }

                .small-text{
                    padding-left: 3rem;
                    position: relative;
                    text-transform: uppercase;
                    font-size: 1.2rem;
                    color: var(--color-gray-1);
                    letter-spacing: 2px;

                    &::before{
                        content: '';
                        position: absolute;
                        left: 0;
                        top: 15px;
                        width: 2rem;
                        height: 2px;
                        background-color: var(--color-gray-5);                  
                    }
                }
            }
        }
    }

    .left-about{
        padding-right: 5rem;

        p{
            font-size: .9rem;
            line-height: 2rem;
            // padding: 1rem;
            padding: 1rem 1.5rem 2rem 0;
            color: var(--color-gray-1);
        }

        h4{
            font-size: 2rem;
            text-transform: uppercase;
        }
    }
}

.about-stats{
    padding-bottom: 4rem;

    .progress-bars{
        display: grid;
        grid-template-columns: repeat(2,1fr);
        grid-gap:2rem;

        .progress-bar{
            display: flex;
            flex-direction: column;

            .prog-title{
                text-transform: uppercase;
                font-weight: 500;
            }

            .progress-con{
                display: flex;
                align-items: center;

                .prog-text{
                    color: var(--color-gray-2);
                }

                .progress{
                    width: 100%;
                    height: .45rem;
                    background-color: var(--color-gray-4);
                    margin-left: 1rem;
                    position: relative;

                    span{
                        position: absolute;
                        top: 0;
                        left: 0;
                        height: 100%;
                        background-color: var(--color-secondary);
                        transition: all .4s ease-in-out;
                        
                    }
                    .html{
                        width: 75%;
                    }
                    .css{
                        width: 75%;
                    }
                    .js{
                        width: 55%;
                    }
                    .c{
                        width: 75%;
                    }
                    .java{
                        width: 75%;
                    }
                    .py{
                        width: 60%;
                    }
                    .reactjs{
                        width: 60%;
                    }
                    .sql{
                        width: 85%;
                    }
                    .sb{
                        width: 50%;
                    }
                     
                }
            }
        }
    }
}

.stat-title{
    text-transform: uppercase;
    font-size: 1.4rem;
    text-align: center;
    padding: 1.5rem 0;
    position: relative;

    &::before{
        content: '';
        position: absolute;
        left: 50%;
        top: 0;
        width: 40%;
        height: 1px;
        background-color: var(--color-gray-5);
        transform: translateX(-50%);

    }
}

/* Timeline */

.timeline{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-gap:2rem;
    padding-bottom: 3rem;
    padding-left: 1.5rem;

    .timeline-item{
        position: relative;
        padding-left: 3rem;
        border-left: 1px solid var(--color-gray-5);

        .t1-icon{
            position: absolute;
            left: -27px;
            top: 0;
            background-color: var(--color-secondary);
            width: 50px;
            height: 50px;
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            i{
                font-size: 1.3rem;
            }
        }
        .t1-duration{
            padding: .2rem .6rem;
            background-color: var(--color-gray-5);
            border-radius: 15px;
            display: inline-block;
            font-size: .8rem;
            text-transform: uppercase;
            font-weight: 500;
        }
        h5{
            padding: 1rem 0;
            text-transform: uppercase;
            font-size: 1rem;
            font-weight: 600;
            
            span{
                color: var(--color-gray-2);
                font-weight: 500;
                font-size: .9rem;
            }
            
            
        }
       
        p{
            color: var(--color-gray-2);
        }
    }
}

/* portfolios */
.port-text {
    padding: 2rem 0;
    text-align: center;
}

.portfolios {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2rem;
    margin-top: 3rem;
    .portfolio-item {
        position: relative;
        border-radius: 15px;
        img {
            width: 100%;
            height: 300px;
            object-fit: cover;
            border-radius: 15px;
        }
        .hover-items {
            width: 100%;
            height: 100%;
            background-color: var(--color-secondary);
            position: absolute;
            left: 0;
            top: 0;
            border-radius: 15px;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            opacity: 0;
            transform: scale(0);
            transition: all .4s ease-in-out;
            h3 {
                font-size: 1.5rem;
                color: var(--color-white);
                margin-bottom: 1.5rem;
            }
            .icons {
                display: flex;
                justify-content: center;
                align-items: center;
                .icon {
                    background-color: var(--color-primary);
                    border-radius: 50%;
                    width: 50px;
                    height: 50px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    margin: 0 1rem;
                    cursor: pointer;
                    transition: all .4s ease-in-out;
                    i {
                        font-size: 1.5rem;
                        color: var(--color-white);
                        margin: 0 1rem;
                    }
                    &:hover {
                        background-color: var(--color-white);
                        i { color: var(--color-primary); }
                    }
                }
            }
        }
    }
}

.portfolio-item:hover .hover-items {
    opacity: 1;
    transform: scale(1);
}

/* Contact */

.contact-content-con{
    display: flex;
    padding-top: 3.5rem;

    .left-contact {
        flex: 2;
        h4 {
            margin-top: 1rem;
            font-size: 2rem;
            text-transform: uppercase;
        }
        p {
            margin: 1rem 0;
            line-height: 2rem;
        }

        .contact-info {
            .contact-item {
                display: flex;
                align-items: center;
                justify-content: space-between;
                p {
                    margin: .3rem 0 !important;
                    padding: 0 !important;
                }
                .icon {
                    display: grid;
                    grid-template-columns: 40px 1fr;
                    i {
                        display: flex;
                        align-items: center;
                        font-size: 1.3rem;
                    }
                }
            }
        }

        .contact-icon {
            display: flex;
            margin-top: 2rem;
            a {
                display: flex;
                align-items: center;
                color: var(--color-white);
                background-color: var(--color-gray-5);
                cursor: pointer;
                justify-content: center;
                width: 50px;
                height: 50px;
                border-radius: 50%;
                margin: 0 .4rem;
                transition: all .4s ease-in-out;
                &:hover {
                    background-color: var(--color-secondary);
                    i { color: var(--color-primary); }
                }
                i {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    font-size: 1.3rem;
                }
            }
        }
    }


    .right-contact{
        flex: 3;
        margin-left: 3rem;

        .input-control{
            margin: 1.5rem 0;

            input,textarea{
                border-radius: 30px;
                font-weight: inherit;
                font-size: inherit;
                font-family: inherit;
                padding: .8rem 1.1rem;
                outline: none;
                border: none;
                background-color: var(--color-gray-5);
                width: 100%;
                color: var(--color-white);
                resize: none;
            }
        }

        .i-c-2{
            display: flex;

            :last-child{
                margin-left: 1.5rem;
            }
        }

        .main-btn{
            .btn-text{
                padding: .7rem 2rem;    
            }
        }
    }
}

/* independent components */

.btn-con, .submit-btn{
    display: flex;
    align-self: flex-start;
}

.main-btn{
    border-radius: 30px;
   
    color: inherit;
    font-weight: 600;
    position: relative;
    border: 1px solid var(--color-secondary);
    display: flex;
    align-self: flex-start;
    display: flex;
    align-items: center;
    overflow: hidden;
    flex-wrap: nowrap;

    .btn-text{
        
        padding: 0 2rem;
       
    }

    .btn-icon{
        
        width: 27.5%;
        background-color: var(--color-secondary);
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        padding: 1rem;
    }
    &::before{
        content: ' ';
        position: absolute;
        top: 0;
        right: 0;
        transform: translateX(100%);
        transition: all .4s ease-out;
        z-index: -1;
    }
    &:hover{
        &::before{
            width: 100%;
            height: 100%;
            background-color: var(--color-secondary);
            transform: rotateX(0);
        }
    }
}

.main-title{
    text-align: center;
    h2{
        position: relative;
        text-transform: uppercase;
        font-size: 4rem;
        font-weight: 700;

    }
    span{
        color: var(--color-secondary);
    }
    .bg-text{
        position: absolute;
        top: 50%;
        left: 50%;
        color: var(--color-gray-5);
        transition: all .4s ease-in-out;
        z-index: -1;
        transform: translate(-50%, -50%);
        font-weight: 800;
    }
}